
import React, {useState, useEffect} from 'react'
import Note from './components/Note'
import Header from "./components/Header"
import noteService from "./services/notes"
import Notification from "./components/Notification"
import Footer from "./components/Footer"

function App() {
    const [notesAsState, setNotes] = useState([])
    const [newNote, setNewNote] = useState("")
    const [showAll, setShowAll] = useState(true)
    const [errorMessage, setErrorMessage] = useState(null)

    useEffect(() => {
        noteService
          .getAll()
          .then(initialNotes => {
            setNotes(initialNotes)
          })
      }, [])
    console.log("render", notesAsState.length, "notes")

    const toggleImportanceOf = id => {
        
        const url = `http://localhost:3001/notes/${id}`
        const note = notesAsState.find(n => n.id === id)
        const changedNote = {...note, important: !note.important}


        noteService
            .update(id, changedNote)
            .then(returnedNote => {
                setNotes(notesAsState.map(n => n.id !== id ? n : returnedNote))
            })
            .catch(error =>{
                setErrorMessage(
                    `Note "${note.content}" was already removed from server`
                )
                setTimeout(() =>{
                    setErrorMessage(null)
                }, 5000)
                    setNotes(notesAsState.filter(n => n.id !== id))
            })
        
    }

    const rows = () => {
      let noteElements = notesToShow.map((note)=> {
       
          return <Note
          key = {note.id}
          note = {note}
          toggleImportance = {() => toggleImportanceOf(note.id)}/>
    })
      //console.log("mapped notes is:", noteElements[0])
      
      return noteElements;
  }
    function addNote(event) {
        event.preventDefault()
        console.log("button clicked", event.target)
        const noteObject = {
            
            content: newNote,
            date: new Date().toISOString(),
            important: Math.random() > 0.5
        }
       
        noteService
            .create(noteObject)
            .then(returnedNote => {
                setNotes(notesAsState.concat(returnedNote))
                setNewNote("")
            })

    }
    const handleNoteChange = (event) => {
        console.log("eventti on: ", event)
        console.log(event.target.value)
        setNewNote(event.target.value)
    }

    const notesToShow = showAll 
        ? notesAsState 
        : notesAsState.filter((note) => note.important)


  return (
      <div>
          <Header/>
          <h1>Notes</h1>

          <Notification message= {errorMessage}/>

          <div>
              <button onClick={() => setShowAll(!showAll)}>
                  show {showAll ? "important" : "all"}
              </button>
          </div>
          <ul>
              {rows()}
          </ul>
          <form onSubmit = {addNote}>
              <input value = {newNote} onChange={handleNoteChange}/>
              <button type = "submit">Save</button>
          </form>
          <Footer/>
      </div>
  )

}

export default App